function ExcelView(allIssues) {
    this.allIssues = allIssues;
};

ExcelView.dateTimeFormat = 'YYYY-MM-DD HH:mm:ss';

// see also PivotKey.makeIssueFieldKey
ExcelView.prototype.fieldValue = function(issue, fieldName) {
    var NULL_VALUE = "";
    var sprintRegex = /com\.atlassian\.greenhopper\.service\.sprint\.Sprint@[\d\w]+\[(.+)?\]/;

    var getNameOrKey = function(field) {
        var m;
        if (typeof field === 'object' && field.hasOwnProperty('name')) {
            return field.name;
        } else if (typeof field === 'object' && field.hasOwnProperty('key')) {
            return field.key;
        } else if (typeof field === 'object' && field.hasOwnProperty('value')) {
            return field.value + (field.hasOwnProperty('child') ? ':' + getNameOrKey(field.child) : '');
        } else if (typeof field == 'string' && (m = field.match(sprintRegex))) {
            var splits = m[1].split(',');
            for (var i = 0; i < splits.length; i++) {
                var split = splits[i];
                if (split.startsWith('name=')) {
                    return split.substr(5);
                }
            }
            return NULL_VALUE;
        } else {
            return field;
        }
    };

    var getValue = function(field) {
        if (TimesheetUtils.isDate(field)) {
            return moment(field).format(ExcelView.dateTimeFormat);
        } else if (Array.isArray(field)) {
            var value = '';
            field.forEach(function(v) {
                if (value.length > 0) {
                    value += ', ';
                }
                value += getNameOrKey(v);
            });
            return value;
        } else {
            return getNameOrKey(field);
        }
    };

    var field = issue.fields[fieldName] || (fieldName == 'parent' ? {key : issue.key} : NULL_VALUE);
    this.field = fieldName != 'project' ? field : NULL_VALUE;
    var value = getValue(this.field);
    if (['timeestimate', 'timeoriginalestimate', 'timespent'].indexOf(fieldName) != -1 && value != NULL_VALUE) {
        value = (Math.round((value || 0) / 36) / 100).toLocaleString() + 'h';
    }
    return value;
};
/*
    Provides raw worklogs.

    Supported options: pivotTableType, monthView, startDate, endDate, user
    TODO? groups, sumSubTask
*/
ExcelView.prototype.generate = function(options) {
    var pivotTable = PivotTableFactory.createPivotTable(options);
    var excel = '<style>\n'
    excel += '<!--br\n';
    excel += '    {mso-data-placement:same-cell;}\n';
    excel += '<!--table\n';
    var separators = (1234.5).toLocaleString().match(/\d+(\D)?\d+(\D)\d+/);
    excel += '    {mso-displayed-decimal-separator:"\\' + separators[2] + '";';
    excel += (separators[1] ? '\n    mso-displayed-thousand-separator:"\\' + separators[1] + '";' : '') + '}\n';
    excel += '-->\n';
    excel += '</style>\n';
    excel += "<table>\n";
    excel += "  <tr>\n";
    excel += "    <td>Project</td>\n";
    excel += "    <td>Type</td>\n";
    excel += "    <td>Key</td>\n";
    excel += "    <td>Title</td>\n";
    for (var i = 0; i < options.moreFields.length; i++) {
        var moreField = options.moreFields[i];
        if (moreField.id != 'workeduser') {
            excel += "    <td>" + moreField.label + "</td>\n";
        }
    }
    excel += "    <td>Created</td>\n";
    excel += "    <td>Started</td>\n";
    excel += "    <td>Username</td>\n";
    excel += "    <td>Display Name</td>\n";
    excel += "    <td>Timespent (h)</td>\n";
    excel += "    <td>Comment</td>\n";
    excel += "  </tr>\n";
    for (var i in this.allIssues) {
        var pivotEntries = pivotTable.add(this.allIssues[i]);
        for (var i in pivotEntries) {
            excel += "  <tr>\n";
            var pivotEntry = pivotEntries[i];
            // rowKey is PivotKey.Issue
            var issue = pivotEntry.rowKey.issue;
            var worklog = pivotEntry.rowKey.worklog;
            excel += "    <td>" + issue.fields.project.name + "</td>\n";
            excel += "    <td>" + issue.fields.issuetype.name + "</td>\n";
            excel += "    <td>" + issue.key + "</td>\n";
            excel += "    <td>" + issue.fields.summary + "</td>\n";
            for (var i = 0; i < options.moreFields.length; i++) {
                var moreField = options.moreFields[i];
                if (moreField.id != 'workeduser') {
                    excel += "    <td>" + this.fieldValue(issue, moreField.id) + "</td>\n";
                }
            }
            excel += "    <td>" + moment(worklog.created).format(ExcelView.dateTimeFormat) + "</td>\n";
            excel += "    <td>" + moment(worklog.workDate).format(ExcelView.dateTimeFormat) + "</td>\n";
            excel += "    <td>" + worklog.author.name + "</td>\n";
            excel += "    <td>" + (worklog.author.displayName || '') + "</td>\n";
            excel += "    <td>" + (Math.round(worklog.timeSpentSeconds / 36) / 100).toLocaleString() + "</td>\n";
            excel += "    <td>" + worklog.comment + "</td>\n";
            excel += "  </tr>\n";
        }
    }
    excel += "</table>\n";
    return excel;
};
ExcelView.prototype.download = function(options) {
    var excel = this.generate(options);
    var excelFile = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40">\n';
    excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel; charset=utf-8">';
    excelFile += "<head>\n";
    excelFile += "<!--[if gte mso 9]>\n";
    excelFile += "<xml>\n";
    excelFile += "<x:ExcelWorkbook>";
    excelFile += "<x:ExcelWorksheets>";
    excelFile += "<x:ExcelWorksheet>";
    excelFile += "<x:Name>";
    excelFile += "{worksheet}";
    excelFile += "</x:Name>";
    excelFile += "<x:WorksheetOptions>";
    excelFile += "<x:DisplayGridlines/>";
    excelFile += "</x:WorksheetOptions>";
    excelFile += "</x:ExcelWorksheet>";
    excelFile += "</x:ExcelWorksheets>";
    excelFile += "</x:ExcelWorkbook>\n";
    excelFile += "</xml>\n";
    excelFile += "<![endif]-->\n";
    excelFile += "</head>\n";
    excelFile += "<body>\n";
    excelFile += excel;
    excelFile += "</body>\n";
    excelFile += "</html>\n";

    var fileName = 'exportData.xls';
    var isIE = /*@cc_on!@*/false || !!document.documentMode; // At least IE6
    if (isIE) {
        var fileData = ['\ufeff' + excelFile];
        var blobObject = new Blob(fileData);
        window.navigator.msSaveOrOpenBlob(blobObject, fileName);
    } else {
        var base64data = "base64," + AJS.$.base64.encode(excelFile);
        var data = 'data:application/vnd.ms-excel;charset=utf-8;filename=exportData.xls;' + base64data;
        var link = document.createElement('a');
        if (typeof link.download === 'string') {
            link.href = data;
            link.download = fileName;
            //Firefox requires the link to be in the body
            document.body.appendChild(link);
            link.click();
            setTimeout(function() {
                document.body.removeChild(link);
            }, 100);
        } else {
            window.open(data);
        }
    }
};


define("app/pivottable/ExcelView", function(){});

